<template>
    <div class="setting">
        <ul>
            <li>
              <router-link tag="div" to="/quality" class="item">
                资质牌照
              </router-link>
            </li>
        </ul>
        <div class="login-out" @click="loginOut" v-if="token">退出登录</div>
      <m-prompt
        v-show="loginOutShow"
        @handleBtnLeft="handleCancel"
        @handleBtnRight="handleLoginOUt"
        :btnLeftText="'取消'"
        :btnRightText="'退出'"
        :content="'是否退出登录？'">
      </m-prompt>
    </div>
</template>

<script>
import mPrompt from '@/components/base/mPrompt'
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'setting',
  computed: {
    ...mapGetters(['userInfo', 'token'])
  },
  data () {
    return {
      loginOutShow: false
    }
  },
  methods: {
    handleCancel () {
      this.loginOutShow = false
    },
    handleLoginOUt () {
      this.Logout()
      this.$router.push('/personalCenter')
    },
    loginOut () {
      this.loginOutShow = true
    },
    ...mapActions([
      'Logout'
    ])
  },
  components: {
    mPrompt
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
  .setting{
    ul{
      margin-bottom: pxToRem(40px);
      li{
        padding:0 pxToRem(20px) 0 pxToRem(30px);
        background: #fff;
        height: pxToRem(101px);
        line-height: pxToRem(101px);
        .item{
          background:#fff  url("../../assets/images/icon_right.png") no-repeat right  center;
          background-size: pxToRem(44px) pxToRem(44px);
          font-size: pxToRem(30px);
          color: #333333;
        }
      }
    }
    .login-out{
      text-align: center;
      background: #fff;
      height: pxToRem(101px);
      line-height: pxToRem(101px);
      font-size:  pxToRem(34px);
      color: #333333;
    }
  }

</style>
